import { Component, ViewChild } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";
import { DialogComponent } from "src/app/shared/components/dialog.component";
import { FormGroup } from "@angular/forms";
import { indicate, handleError } from "src/app/shared/operators/operators";
import { take } from "rxjs/operators";
import { AlertType } from "src/app/shared/modules/alert/common/alert-type.model";
import { BarcodeFormat, Exception } from "@zxing/library";
import { Person } from "src/app/shared/models/people-management/person.model";
import { PersonService } from "src/app/shared/services/people-management/person/person.service";
import { ZXingScannerComponent } from "@zxing/ngx-scanner";
import { PersonInfo } from "./person-info.model";

@Component({
  selector: "app-laboratory-management-analyses-create-scan",
  templateUrl: "./scan.component.html",
  styleUrls: ["scan.component.scss"],
  providers: [PersonService],
})
export class ScanComponent extends DialogComponent<ScanComponent, Person> {
  allowedFormats = [BarcodeFormat.PDF_417];
  personInfo: PersonInfo | null = null;
  mediaDevices: MediaDeviceInfo[] = [];

  @ViewChild("scanner") scanner: ZXingScannerComponent | undefined;

  /**
   * Constructor
   * @param personService
   * @param dialog
   */
  constructor(
    private personService: PersonService,
    private dialog: MatDialogRef<ScanComponent>
  ) {
    super(dialog, new FormGroup({}));
  }

  /**
   * On camers found
   * @param mediaDevices
   */
  onCamerasFound(mediaDevices: MediaDeviceInfo[]): void {
    this.mediaDevices = mediaDevices;
    if (this.scanner) {
      //this.scanner?.hints.set(DecodeHintType.CHARACTER_SET, "iso-8859-1");
      //console.log(3);
    }
  }

  /**
   * Change media device
   * @param mediaDeviceInfo
   */
  changeMediaDevice(mediaDeviceInfo: MediaDeviceInfo): void {
    if (!this.scanner) {
      return;
    }
    this.scanner.device = mediaDeviceInfo;
  }

  /**
   * Camera not found
   */
  onCamerasNotFound(): void {
    this.alert$.next({
      message: "Camera could not be found",
      type: AlertType.error,
    });
  }

  /**
   * On scan success
   * @param str
   */
  onScanSuccess(str: string): void {
    const personInfo = this.getPersonFromSyrianId(str);
    if (!personInfo) {
      return;
    }
    console.log(personInfo);
    this.personInfo = personInfo;
    //this.handleSubmit();
  }

  /**
   * On scan error
   * @param _error
   */
  onScanError(_error: Error): void {
    //
  }

  /**
   * On scan failure
   * @param _exception
   */
  onScanFailure(_exception: Exception | undefined): void {
    //
  }

  /**
   * Get person from Syrian ID
   * @param input
   * @returns
   */
  private getPersonFromSyrianId(input: string): PersonInfo | null {
    const bytes = this.toUint8Array(input);
    // split using #
    const items: Uint8Array[] = [];
    let posStart = 0;
    let posStop = 0;
    while (bytes.length >= posStart && bytes.length > 0) {
      posStop = bytes.indexOf(35, posStart);
      if (posStop < 0) {
        posStop = bytes.length;
      }
      items.push(bytes.slice(posStart, posStop));
      posStart = posStop + 1;
    }
    console.log(items);
    if (items.length != 11) {
      return null;
    }
    const personInfo: PersonInfo = {
      firstName: this.decodeUsingISO88591Windows1256(items[0]),
      lastName: this.decodeUsingISO88591Windows1256(items[1]),
      fatherFirstName: this.decodeUsingISO88591Windows1256(items[2]),
      motherName: this.decodeUsingISO88591Windows1256(items[3]),
      nationalNumber: this.decodeUsingISO88591Windows1256(items[5]),
    };

    return personInfo;
  }

  /**
   * Decode using iso 8859-1 and windows 1256
   * @param input
   * @returns
   */
  private decodeUsingISO88591Windows1256(input: Uint8Array): string {
    const iso8859Decoder = new TextDecoder("iso-8859-1");
    const iso8859Text = iso8859Decoder.decode(input);

    // decode using windows 1256
    const windows1256Decoder = new TextDecoder("windows-1256");
    return windows1256Decoder.decode(this.toUint8Array(iso8859Text));
  }

  /**
   * To Uint8 array
   * @param input
   * @returns
   */
  private toUint8Array(input: string): Uint8Array {
    return Uint8Array.from(input.split("").map((x: any) => x.charCodeAt()));
  }

  // check if person exists
  private findPerson() {
    this.personService.getPersons({
      firstName: this.personInfo?.firstName,
      lastName: this.personInfo?.lastName,
      pageIndex: 1,
      pageSize: 50,
    });
  }

  /**
   * Submit
   */
  override handleSubmit(): void {
    if (!this.personInfo) {
      this.alert$.next({
        message: "Could not read the code",
        type: AlertType.error,
      });
      return;
    }
    this.personService
      .createPerson({
        firstName: this.personInfo.firstName,
        lastName: this.personInfo.lastName,
        nationalNumber: this.personInfo.nationalNumber,
        motherName: this.personInfo.motherName,
        sexId: "",
      })
      .pipe(indicate(this.indicator$))
      .pipe(handleError(this.alert$))
      .pipe(take(1))
      .subscribe((res) => {
        this.close(res);
      });
  }
}
