import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { PagedResponse } from "src/app/shared/models/shared/paged.response";
import { GetPersonsRequest, CreatePersonRequest } from "./person.input";
import { Person } from "src/app/shared/models/people-management/person.model";

/**
 * Person Service
 */
@Injectable()
export class PersonService {
  path = `v1/people-management/persons`;
  constructor(private readonly http: HttpClient) {}

  /**
   * Get analyses
   */
  getPersons(input: GetPersonsRequest): Observable<PagedResponse<Person>> {
    return this.http.get<PagedResponse<Person>>(`${this.path}`, {
      params: new HttpParams({ fromObject: input as any }),
    });
  }

  /**
   * Get person
   */
  getPerson(analysisId: string): Observable<Person> {
    return this.http.get<Person>(`${this.path}/${analysisId}`);
  }

  /**
   * Create person
   */
  createPerson(input: CreatePersonRequest): Observable<Person> {
    return this.http.post<Person>(`${this.path}`, input);
  }
}
