import { Component, Inject } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { FormBuilder } from "@angular/forms";
import { DialogComponent } from "src/app/shared/components/dialog.component";
import { map } from "rxjs/operators";
import { AnalysesFilter } from "./Analyses.filter";
import { Observable } from "rxjs/internal/Observable";
import { AnalysisType } from "src/app/shared/models/analysis-management/analysis-type.model";
import { AnalysisTypeService } from "src/app/shared/services/analysis-management/analysis-type/analysis-type.service";

@Component({
  selector: "app-laboratory-management-analyses-filter",
  templateUrl: "./filter.component.html",
  providers: [AnalysisTypeService],
})
export class FilterComponent extends DialogComponent<
  FilterComponent,
  AnalysesFilter
> {
  analysisTypes$: Observable<AnalysisType[] | null> | null = null;

  /**
   * Constructor
   * @param fb
   * @param dialog
   * @param data
   */
  constructor(
    private fb: FormBuilder,
    private dialog: MatDialogRef<FilterComponent>,
    private analysisTypeService: AnalysisTypeService,
    @Inject(MAT_DIALOG_DATA) public data: AnalysesFilter
  ) {
    super(
      dialog,
      fb.group({
        analysisTypeId: [data.analysisTypeId],
      })
    );
    this.getSampleTypes();
  }

  /**
   * Get sample types
   */
  private getSampleTypes(): void {
    this.analysisTypes$ = this.analysisTypeService
      .getAnalysisTypes({
        pageIndex: 1,
        pageSize: 50,
        sortBy: "Name",
        sortDescending: false,
      })
      .pipe(map((x) => x.items));
  }

  /**
   * Submit
   */
  override handleSubmit(): void {
    this.close(this.form.value);
  }
}
