import { Component, OnInit } from "@angular/core";
import { UntypedFormBuilder, Validators, FormGroup } from "@angular/forms";
import { Observable, Subject } from "rxjs";
import { debounceTime, map, startWith, switchMap, take } from "rxjs/operators";
import { handleError, indicate } from "src/app/shared/operators/operators";
import { Alert } from "src/app/shared/modules/alert/common/alert.model";
import { Router } from "@angular/router";
import { AnalysisService } from "src/app/shared/services/analysis-management/analysis/analysis.service";
import { AnalysisType } from "src/app/shared/models/analysis-management/analysis-type.model";
import { PersonService } from "src/app/shared/services/people-management/person/person.service";
import { CreateAnalysisRequest } from "src/app/shared/services/analysis-management/analysis/analysis.input";
import { Person } from "src/app/shared/models/people-management/person.model";
import { ScanComponent } from "./components/scan/scan.component";
import { MatDialog } from "@angular/material/dialog";
import { AnalysisTypeService } from "src/app/shared/services/analysis-management/analysis-type/analysis-type.service";

@Component({
  selector: "app-analyses-create",
  templateUrl: "./create.component.html",
  providers: [AnalysisService, AnalysisTypeService, PersonService],
})
export class CreateComponent implements OnInit {
  analysisTypes$: Observable<AnalysisType[] | null> | null = null;
  persons$: Observable<Person[] | null> | null = null;
  indicator$ = new Subject<boolean>();
  alert$ = new Subject<Alert | undefined>();
  form: FormGroup;

  constructor(
    private analysisService: AnalysisService,
    private analysisTypeService: AnalysisTypeService,
    private personService: PersonService,
    private dialog: MatDialog,
    private fb: UntypedFormBuilder,
    private router: Router
  ) {
    this.form = this.fb.group({
      person: [null, [Validators.required]],
      analysisType: [null, [Validators.required]],
      notes: [null, []],
    });
  }

  ngOnInit(): void {
    this.getAnalysisTypes();
    this.getPersons();
  }

  /**
   * Review
   */
  review(): { title: string; items: { key: string; value: string }[] }[] {
    const review = [];
    review.push({
      title: "",
      items: this.generatePersonReview(),
    });
    return review;
  }

  /**
   * Submit
   */
  submit(): boolean {
    // validate
    if (!this.validate()) {
      return false;
    }

    // prepare input
    const input: CreateAnalysisRequest = {
      personId: this.form.value.personId,
      analysisTypeId: this.form.value.analysisTypeId,
      notes: this.form.value.notes,
    };

    this.analysisService
      .createAnalysis(input)
      .pipe(indicate(this.indicator$))
      .pipe(handleError(this.alert$))
      .pipe(take(1))
      .subscribe(() => {
        this.router.navigate(["/analyses"]);
      });
    return false;
  }

  /**
   * Display data unit
   * @param item
   * @returns
   */
  displayPerson(item: Person): string {
    return item ? `${item.firstName} ${item.lastName}` : "";
  }

  /**
   * Get analyses types
   */
  private getAnalysisTypes(): void {
    this.analysisTypes$ = this.analysisTypeService
      .getAnalysisTypes({
        pageIndex: 1,
        pageSize: 10,
        sortBy: "Name",
        sortDescending: false,
      })
      .pipe(map((x) => x.items));
  }

  /**
   * Get persons
   */
  private getPersons(): void {
    this.persons$ = this.form.controls.person.valueChanges.pipe(
      startWith(""),
      debounceTime(500),
      switchMap((value: string) =>
        this.personService
          .getPersons({
            firstName: value,
            pageIndex: 1,
            pageSize: 10,
            sortBy: "FirstName",
            sortDescending: false,
          })
          .pipe(map((x) => x.items))
      )
    );
  }

  /**
   * Open scan component
   */
  openScanComponent(): void {
    const ref = this.dialog.open(ScanComponent, {
      autoFocus: true,
      width: "480px",
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((res: Person) => {
        if (res) {
          this.form.controls.person.setValue(res);
        }
      });
  }

  /**
   * Validate
   */
  private validate(): boolean {
    if (this.form && !this.form.valid) {
      Object.keys(this.form.controls).forEach((field) => {
        const control = this.form?.get(field);
        control?.markAsTouched({ onlySelf: true });
      });
      return false;
    }
    if (!this.form.value.person.id) {
      return false;
    }
    return true;
  }

  /**
   * Generate person review
   */
  private generatePersonReview(): { key: string; value: string }[] {
    return [
      {
        key: "",
        value: this.form.value.person?.firstName,
      },
    ];
  }
}
