import { Component } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { take } from "rxjs/operators";
import { indicate } from "src/app/shared/operators/operators";
import { TableComponent } from "src/app/shared/components/table.component";
import { CreateComponent } from "./components/create/create.component";
import { AnalysisService } from "src/app/shared/services/analysis-management/analysis/analysis.service";
import { GetAnalysesRequest } from "src/app/shared/services/analysis-management/analysis/analysis.input";
import { FilterComponent } from "./components/filter/filter.component";
import { Analysis } from "src/app/shared/models/analysis-management/analysis.model";

@Component({
  selector: "app-laboratory-management-analyses",
  templateUrl: "./analyses.component.html",
  styleUrls: ["analyses.component.scss"],
})
export class AnalysesComponent extends TableComponent<Analysis> {
  displayedColumns = ["name", "analysisType", "menu"];

  /**
   * Constructor
   * @param dialog
   * @param analysisService
   */
  constructor(
    private dialog: MatDialog,
    private analysisService: AnalysisService
  ) {
    super();
    this.get();
  }

  /**
   * Open create component
   */
  openCreateComponent(): void {
    const ref = this.dialog.open(CreateComponent, {
      autoFocus: true,
      width: "480px",
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((res) => {
        if (res) {
          this.add(res);
        }
      });
  }

  /**
   * Open filter component
   */
  openFilterComponent(): void {
    const ref = this.dialog.open(FilterComponent, {
      autoFocus: true,
      width: "480px",
      data: this.filter,
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((filter) => {
        if (filter) {
          this.filter = filter;
          this.get();
        }
      });
  }

  /**
   * Get
   */
  get(): void {
    const input: GetAnalysesRequest = {
      pageIndex: this.pageIndex,
      pageSize: this.pageSize,
      sortBy: this.sortBy,
      sortDescending: this.sortDescending,
    };

    this.analysisService
      .getAnalyses(input)
      .pipe(indicate(this.indicator$))
      .pipe(take(1))
      .subscribe((res) => {
        this.set(res.count, res.items);
      });
  }
}
